// =====================  Media  ==========================

/*! Media */
@media screen and (max-width: 1200px) {
  .first-screen__container {
    max-width: 100%;
    margin: 0 auto; }

  .first-screen__form {
    width: 35%;
    height: auto;
    padding: 0 0 0 70px;
    //padding: 31px 100px 0 70px
    margin: 0;
    min-height: 349px;
    padding-top: 60px; }
  .right-side {
    min-width: 45%; } }
@media screen and (max-width: 1024px) {

  // .main-slider__item
  //   width: 272px

  .second-screen__form {
    max-width: 69%;
    left: 50%;
    margin-left: -34%; }

  .advantages__right-side {
    width: 50%; }

  .services__items {
    width: 57%; }

  .right-side {
    width: 55%; }

  .first-screen__form {
    width: 39%; } }

@media screen and (max-width: 992px) {
  .sliders__first-slider {
    margin: 0 70px;
    width: 65%; }
  .advantages__left-side {
    width: 43%; }
  .right-side {
    right: 0; }

  .services__items {
    width: 70%; }

  .third-block__text {
    width: 90%; }

  .second-screen__form {
    max-width: 70%;
    margin-left: -35%; }

  .first-screen__form {
    width: 43%; }
  .phone {
    display: none; } }
@media screen and (max-width: 768px) {
  .footer__content {
    flex-direction: column; }
  .sliders__first-slider {
    margin: 0 70px;
    width: 80%; }
  .services__items {
    margin: 0; }
  .contacts__right-side {
    width: 100%; }
  .contacts__right-side__map {
    display: none; }
  .contacts__left-side {
    width: 100%; }
  .contacts__content {
    flex-direction: column-reverse; }
  .advantages-title {
    margin: 0 43px; }
  .advantages__items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .advantages__left-side {
    margin-left: 0; }
  .advantages__item {
    justify-content: left; }
  .advantages__left-side {
    width: 100%; }
  .advantages__right-side {
    width: 100%; }
  .advantages__container {
    flex-direction: column; }
  .right-side {
    right: 0!important;
    padding-top: 35px;
    width: 80%; }

  .services__items {
    width: 80%; }

  .second-screen__form {
    max-width: 84%;
    margin-left: -41%; }

  .content___text-block {
    p {
      padding: 32px 70px;
      max-width: 100%; } }

  .first-screen__form {
    width: 70%; }

  .header__menu {
    height: 240px;
    nav {
      ul {
        flex-wrap: wrap;
        flex-direction: column;
        li {
          margin: 10px 0; } } } } }
@media screen and (max-width: 576px) {
  .second-screen__container {
    margin-bottom: 233px; }
  .main-slider__items {
    margin: 0 15px; }
  .footer-phone {
    display: block!important; }
  // .main-slider__items
  //   margin: 0 42px
  .footer__items {
    ul {
      flex-wrap: wrap; } }

  .footer__content {
    margin: 0 10px; }
  .first-slider__content {
    padding: 50px 33px 20px 0; }

  .sliders__first-slider {
    margin: 0 0px;
    padding: 0 18px;
    width: 100%; }
  .contacts__left-side {
    padding: 32px 18px; }
  .contacts__right-side__adress {
    padding: 65px 18px; }
  .advantages__item {
    width: 90%; }
  .advantages__items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .advantages__left-side {
    margin-left: 0; }

  .adv__image {
    display: none; }

  .content__services__title {
    h2 {
      margin: 0 18px;
      font-size: 18px; } }

  .services__items {
    width: 100%;
    margin: 0;
    // padding: 30px 47px
    padding: 31px 17px; }


  .third-block__title {
    padding: 32px 18px; }

  .second-screen__form {
    height: auto; }

  .third-block__text {
    width: 100%; }

  .third-block__container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0; }

  .second-screen__form {
    max-width: 100%;
    margin-left: -50%; }

  .content___text-block {
    p {
      font-size: 18px;
      padding: 32px 18px; } }

  .first-screen__form {
    padding: 60px 10px 0 18px; }

  .first-screen__form {
    width: 100%; }

  .header__menu {
    height: 240px; }

  .header__content {
    margin: 0 15px; } }

@media screen and (max-width: 320px) {
  .content___text-block {
    p {
      font-size: 16px; } }
  .header,
  .footer {
    background: #fff; } }
/*! Media End*/
