/*! .footer */
.contacts__container {
    max-width: 1200px;
    margin: 0 auto; }

.contacts__content {
    display: flex; }


.contacts__left-side {
    transition: linear .5s;
    background: #F9F9F9;
    padding: 32px 70px;
    width: 40%; }

.contacts__form {
    h3 {
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 150%;
        color: #646464; }
    p {
        padding-top: 32px;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 150%;
        color: #008080; } }
.textarea {
    background: #FFFFFF;
    padding: 12px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    color: #AAAAAA;
    border: none;
    border-bottom: 1px solid $green;
    margin: 20px 0;
    width: 100%; }
.contacts__right-side {
    width: 60%; }

.contacts__right-side__adress {
    transition: linear .5s;
    padding: 32px 100px;
    span {
        font-style: normal;
        padding: 0 11px 0 0;
        font-weight: normal;
        font-size: 16px;
        line-height: 150%;
        color: #808080; }
    h4 {
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 150%;
        color: #646464; }
    ul {
        li {
            display: flex;
            flex-wrap: nowrap;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            color: #292929;
            margin: 12px 0;
            a {
                font-weight: 500;
                ont-size: 16px;
                line-height: 150%;
                color: #008080; } } } }
.contacts__right-side__map {
    background-image: url('../img/map.png');
    background-size: cover;
    width: 100%;
    height: 300px; }


.footer {
    width: 100%;
    background: #008080; }

.footer__container {
    max-width: 1200px;
    margin: 0 auto; }
.footer__content {
    transition: linear .5s all;
    margin: 0 70px;
    display: flex;
    align-self: center;
    justify-content: space-between; }
.footer__content-part-left {
    transition: linear .5s all; }
.footer__items {
    p {
        padding-top: 20px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 150%;
        color: #F9F9F9; } }

.footer__items {
    ul {
        display: flex;

        li {
            margin: 0 10px;
            padding: 10px 0;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 150%;
            color: #FFFFFF;
            &::nth-child(1) {
                margin: 0; }
            a {
                color: #FFFFFF; } } } }


.footer__content-part-right {
    transition: linear .5s all;
    ul {
        display: flex;
        li {
            transition: linear .5s all;
            a {
                transition: linear .5s all;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 150%;
                display: flex;
                align-items: center;
                text-align: center;
                color: #F9F9F9;
                text-decoration: none;
                padding: 20px 8px;
                &:hover {
                    text-decoration: underline; } } } } }

.footer-phone {
    display: block; }

/*! .footer end*/
