
.content__first-screen {
  width: 100%;
  height: 349px;
  background-image: url('../img/1-bg.png');
  background-size: cover;
  background-position:  50% 50%; }

.first-screen__container {
  max-width: 1200px;
  margin: 0 auto; }

.first-screen__form {
  width: 37%;
  height: 349px;
  padding: 60px 46px 0 70px;
  display: block;
  transition: all linear .5s;
  background: #F9F9F9;
  h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    color: #008080; } }

.form-row {
  display: block; }
.form {
  width: 261px; }

.form-row {
  background: #FFFFFF;
  padding: 12px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  align-items: center;
  color: #AAAAAA;
  border: none;
  border-bottom: 1px solid $green;
  margin: 20px 0;
  width: 100%; }

.button-main {
  //width: 74%
  //border: 1px solid  $green
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;
  background: $green;
  padding: 16px 20px;
  display: block;
  transition: all linear .5s;
  //width: fit-content
  &:hover {
    color: $green;
    text-decoration: none;
    border: 1px solid $green;
    background: #fff;
    display: block; } }





.content___text-block {
  p {
    transition: all linear .5s;
    padding: 32px 70px;
    max-width: 75%;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
    color: #646464; } }
.text-block__container {
  max-width: 1200px;
  margin: 0 auto;
  transition: linear .5s all; }
.second-screen__container {
  background-image: url('../img/bg-2.png');
  background-size: cover;
  background-position: 50% 50%;
  height: 316px;
  margin: 90px 0; }
.content__second-screen {
  width: 100%;
  position: relative;
  // height: 316px
 }  // padding: 80px 0

.second-screen__form {
  height: 500px;
  padding: 24px 0;
  max-width: 55%;
  background: #F9F9F9;
  text-align: center;
  margin: auto;
  position: absolute;
  top: -88px;
  left: 50%;
  margin-left: -25%;
  transition: linear .5s all; }

.second-screen__text-part {
  display: flex;
  padding: 33px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    color: #292929;
    text-align: left; } }
.image-part {
  margin: 15px; }
.second-screen__rows {
  h3 {
    padding-top: 30px;
    padding-bottom: 32px;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    color: #008080; } }

.second-screen__form {
  display: flex;
  flex-direction: column; }

.center {
  margin: 0 auto; }


.third-block__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 70px; }

.third-block__title {
  padding: 32px 0;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 150%;
  color: #808080; }

.third-block__text {
  transition: linear .5s all;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 150%;
  color: #008080;
  padding: 54px 37px;
  background: #F9F9F9;
  width: 73%; }

.services__container {
  max-width: 1200px;
  margin: 0 auto; }

.content__services__title {
  max-width: 1200px;
  margin: 0 auto;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #292929;
  padding: 32px 0;
  h2 {
    margin: 0 70px; } }

.services__items {
  margin: 0 70px; }

.content__services {
  background-image: url('../img/bg-3.png');
  background-size: cover;
  background-position: 50% 50%;
  width: 100%;
  height: 1171px;
  position: relative; }

.services__items {
  width: 48%;
  background: #FFFFFF;
  padding: 30px 106px;
  z-index: 6;
  position: absolute; }


.services__one-item {
  display: flex;
  height: 150px;
  transition: linear .5s all;
  span {
    font-weight: normal;
    font-size: 56px;
    line-height: 150%;
    display: flex;
    align-items: center;
    color: #CCCCCC;
    //padding: 10px 0
    padding: 10px 20px 0 0; }
  p {
    font-style: normal;
    padding: 10px 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    display: flex;
    align-items: center;
    color: #008080;
    border-bottom: 1px solid $green; } }

.right-side {
  width: 45%;
  position: absolute;
  right: 10vw;
  bottom: 0;
  padding-top: 221px;
  z-index: 1!important;
  transition: linear .5s all;
 }  //padding-top: 118px

.advantages__container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex; }

.advantages__left-side {
  transition: linear .6s;
  margin-left: 70px;
  width: 40%; }


.advantages-title {
  padding: 32px 0;
  h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    color: #646464; } }

.advantages__item {
  margin: 20px 0;
  width: 80%;
  padding: 16px 21px;
  background: #F9F9F9;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: linear .5s;
  color: #646464;
  p {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    display: flex;
    align-items: center; }

  &:hover {
    background: $green;
    color: #fff; }
  &:hover .img-svg path {
      fill: #fff;
      stroke: #fff; } }
.advantages_img {
  svg {
    margin: 0 10px; } }


.advantages__right-side {
  transition: linear .6s;
  width: 60%; }

.adv__comment {
  background: #F9F9F9;
  padding: 24px 32px;
  display: flex;
  align-items: center;
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #292929; } }

.quotes_img {
  margin: 0 13px; }
.adv__image {
  img {
    width: 100%; } }

//*****sliders*****//

.content__sliders {
  width: 100%; }
.sliders__container {
  max-width: 1200px;
  margin: 0 auto; }

.sliders__first-slider {
  transition: linear .5s;
  margin: 0 70px;
  width: 46%;
  background: #fff; }
.first-slider__title {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 150%;
  color: #646464;
  padding: 30px 0; }
.first-slider__content {
  padding: 50px 100px 20px 0;
  p {
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
    color: #008080; } }

.first-slider__arrows {
  display: flex;
  justify-content: center; }

.first-slider__arrows {
  transition: linear .5s;
  a {
    transition: linear .5s;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 80px;
    &:hover {
      background: #F9F9F9; }
    &:active {
      background: #F9F9F9; } } }

.sliders__main-slider {
  transition: all .7s ease-in 0.1s;
  background: #F9F9F9; }
// .slide
//   position: absolute
//   width: 100%
//   transition: all .7s ease-in 0.1s
.main-slider__items {
  // position: absolute
  // width: 100%
  // transition: all .7s ease-in 0.1s
  margin: 0 70px;
  display: flex;
  flex-wrap: wrap; }
// .acive-button
//   background: #F9F9F9
.active {
  transition: all .7s ease-in 0.1s; }
.no_active {
  background: #F9F9F9; }
.main-slider-content {
  max-width: 1200px;
  height: 510px;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  padding: 32px 0;
 }  //margin: 0 70px


.main-slider__item {
  display: flex;
  margin: 20px 10px;
  max-width: 100%;
  height: auto;
 }  //width: 320px
.main-slider__item-img {
  max-width: 100%; }
.main-slider__item-text {
  background: #FFFFFF;
  display: flex;
  max-width: 170px;
  transition: linear .7s;
  color: #00C7C7;
  p {
    padding-left: 12px;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center; }
  &:hover {
    color: #fff;
    background: #00C7C7; } }


