/*! Defaults End */
/*! .header */
.header__container {
  max-width: 1200px;
  margin: 0 auto;
}

.header__content {
  display: flex;
  margin: 0 70px;
  justify-content: space-between;
  align-items: center;
  transition: all linear .5s;
}

.header__phones {
  display: flex;
  align-items: center;
  width: 70%;
  justify-content: space-around;
}

.lang a {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  color: #646464;
  transition: all .5s ease-in;
}

.phone a {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 27px;
  color: #008080;
  transition: all .5s ease-in;
}

.phone a:hover {
  text-decoration: none;
}

.phone a svg {
  margin: -2px 15px -2px 0;
}

.header__menu {
  width: 100%;
  height: 80px;
  box-shadow: 0px 0.5px 1px rgba(207, 207, 207, 0.25);
  background: #fff;
  align-items: center;
  justify-content: center;
  transition: all linear .6s;
  position: absolute;
  left: 0;
  display: flex;
  z-index: 999;
}

.header__menu nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
}

.header__menu nav ul li a {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #292929;
  margin: 0 42.5px;
  transition: all linear .5s;
}

.header__menu nav ul li a:hover {
  color: #00C7C7;
  text-decoration: none;
}

.menu-sticky {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
}

/*! .header burger-menu*/
.menu-btn {
  display: inline-block;
  margin: 6px 0;
  padding: 5px 0;
  overflow: visible;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  border: 0;
  background-color: transparent;
  color: inherit;
  font-size: 0;
  text-transform: none;
  cursor: pointer;
}

.menu-btn__inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  background: #008080;
}

.menu-btn__inner::before {
  transition: top 0.075s 0.12s ease, opacity 0.075s ease;
  background: #008080;
}

.menu-btn__inner::after {
  transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  background: #008080;
}

.is-active .menu-btn__inner {
  transform: rotate(45deg);
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-delay: 0.12s;
}

.is-active .menu-btn__inner::before {
  top: 0;
  transition: top 0.075s ease, opacity 0.075s 0.12s ease;
  opacity: 0;
}

.is-active .menu-btn__inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.menu-btn__block {
  display: inline-block;
  position: relative;
  width: 36px;
  height: 25px;
}

.menu-btn__inner {
  display: block;
  position: absolute;
  top: 10px;
  transform: translateY(-50%);
  background: #008080;
  width: 16.5px;
  height: 1.5px;
}

.menu-btn__inner::before, .menu-btn__inner::after {
  position: absolute;
  background: #008080;
  width: 16.5px;
  height: 1.5px;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.menu-btn__inner::before, .menu-btn__inner::after {
  content: "";
  display: block;
  background: #008080;
}

.menu-btn__inner::before {
  top: 6px;
}

.menu-btn__inner::after {
  bottom: 6px;
}

